import { useRef, useEffect, useState } from "react";

interface FormantTrackerProps {
  f1: number;
  f2: number;
}

interface Point {
  x: number;
  y: number;
  f1: number;
  f2: number;
  time: number;
}

// Vowel reference data
const vowelData = [
  { vowel: 'i', f1: 280, f2: 2250, label: 'ee' },
  { vowel: '', f1: 400, f2: 2000, label: 'i' },
  { vowel: 'e', f1: 400, f2: 1900, label: 'ay' },
  { vowel: '', f1: 550, f2: 1800, label: 'eh' },
  { vowel: '', f1: 700, f2: 1700, label: 'a' },
  { vowel: '', f1: 700, f2: 1100, label: 'ah' },
  { vowel: '', f1: 600, f2: 900, label: 'aw' },
  { vowel: 'o', f1: 450, f2: 900, label: 'oh' },
  { vowel: '', f1: 380, f2: 950, label: 'u' },
  { vowel: 'u', f1: 310, f2: 870, label: 'oo' },
  { vowel: '', f1: 600, f2: 1200, label: 'uh' },
  { vowel: '', f1: 500, f2: 1400, label: 'er' }
];

// F1/F2 ranges for normalization
const f1Range = { min: 200, max: 900 };
const f2Range = { min: 700, max: 2500 };

export default function FormantTracker({ f1, f2 }: FormantTrackerProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [canvasSize, setCanvasSize] = useState({ width: 0, height: 0 });
  const currentPointRef = useRef<Point | null>(null);
  const pointHistoryRef = useRef<Point[]>([]);
  const maxHistoryLength = 30;

  // Initialize canvas size
  useEffect(() => {
    const updateSize = () => {
      if (canvasRef.current) {
        const dpr = window.devicePixelRatio || 1;
        const canvas = canvasRef.current;
        const container = canvas.parentElement;
        
        if (container) {
          const width = container.clientWidth;
          const height = container.clientHeight;
          
          // Set canvas size accounting for device pixel ratio
          canvas.width = width * dpr;
          canvas.height = height * dpr;
          
          // Scale context to match CSS size
          const ctx = canvas.getContext('2d');
          if (ctx) {
            ctx.scale(dpr, dpr);
          }
          
          setCanvasSize({ width, height });
        }
      }
    };
    
    // Initial size
    updateSize();
    
    // Update on resize
    const resizeObserver = new ResizeObserver(updateSize);
    if (canvasRef.current?.parentElement) {
      resizeObserver.observe(canvasRef.current.parentElement);
    }
    
    return () => {
      resizeObserver.disconnect();
    };
  }, []);

  // Normalize F1 to x-coordinate (inverted, as higher F1 should be to the right)
  const normalizeF1 = (f1Value: number): number => {
    const normalized = Math.max(0, Math.min(1, (f1Value - f1Range.min) / (f1Range.max - f1Range.min)));
    return normalized * canvasSize.width;
  };
  
  // Normalize F2 to y-coordinate (inverted, as higher F2 should be at the top)
  const normalizeF2 = (f2Value: number): number => {
    const normalized = 1 - Math.max(0, Math.min(1, (f2Value - f2Range.min) / (f2Range.max - f2Range.min)));
    return normalized * canvasSize.height;
  };

  // Update current point when f1 or f2 change
  useEffect(() => {
    if (f1 > 0 && f2 > 0 && canvasSize.width > 0) {
      // Update current point
      currentPointRef.current = {
        x: normalizeF1(f1),
        y: normalizeF2(f2),
        f1,
        f2,
        time: Date.now()
      };
      
      // Add to history
      pointHistoryRef.current.push(currentPointRef.current);
      
      // Remove oldest points if history exceeds max length
      if (pointHistoryRef.current.length > maxHistoryLength) {
        pointHistoryRef.current.shift();
      }
      
      // Redraw chart
      drawChart();
    }
  }, [f1, f2, canvasSize]);

  // Draw vowel chart
  const drawChart = () => {
    if (!canvasRef.current || canvasSize.width === 0) return;
    
    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;
    
    // Clear canvas
    ctx.fillStyle = '#f5f5f5';
    ctx.fillRect(0, 0, canvasSize.width, canvasSize.height);
    
    // Draw grid
    ctx.strokeStyle = '#e0e0e0';
    ctx.lineWidth = 1;
    
    // Vertical grid lines
    for (let i = 0; i <= 4; i++) {
      const x = (i / 4) * canvasSize.width;
      ctx.beginPath();
      ctx.moveTo(x, 0);
      ctx.lineTo(x, canvasSize.height);
      ctx.stroke();
    }
    
    // Horizontal grid lines
    for (let i = 0; i <= 4; i++) {
      const y = (i / 4) * canvasSize.height;
      ctx.beginPath();
      ctx.moveTo(0, y);
      ctx.lineTo(canvasSize.width, y);
      ctx.stroke();
    }
    
    // Draw vowel reference points
    vowelData.forEach(vowel => {
      const x = normalizeF1(vowel.f1);
      const y = normalizeF2(vowel.f2);
      
      // Draw vowel point
      ctx.fillStyle = '#9e9e9e';
      ctx.beginPath();
      ctx.arc(x, y, 5, 0, Math.PI * 2);
      ctx.fill();
      
      // Draw vowel label
      ctx.fillStyle = '#424242';
      ctx.font = '10px sans-serif';
      ctx.textAlign = 'center';
      ctx.fillText(vowel.label, x, y - 8);
    });
    
    // Draw current point history (trail)
    if (pointHistoryRef.current.length > 1) {
      ctx.strokeStyle = 'rgba(63, 81, 181, 0.3)';
      ctx.lineWidth = 2;
      ctx.beginPath();
      ctx.moveTo(pointHistoryRef.current[0].x, pointHistoryRef.current[0].y);
      
      for (let i = 1; i < pointHistoryRef.current.length; i++) {
        ctx.lineTo(pointHistoryRef.current[i].x, pointHistoryRef.current[i].y);
      }
      
      ctx.stroke();
    }
    
    // Draw current point
    if (currentPointRef.current) {
      ctx.fillStyle = 'rgba(255, 64, 129, 0.8)';
      ctx.beginPath();
      ctx.arc(currentPointRef.current.x, currentPointRef.current.y, 8, 0, Math.PI * 2);
      ctx.fill();
      
      ctx.strokeStyle = '#ffffff';
      ctx.lineWidth = 2;
      ctx.stroke();
    }
  };

  // Initial draw
  useEffect(() => {
    if (canvasSize.width > 0) {
      drawChart();
    }
  }, [canvasSize]);

  return (
    <div className="relative w-full mx-auto bg-muted rounded-md" style={{ height: "280px" }}>
      <canvas ref={canvasRef} className="absolute top-0 left-0 w-full h-full"></canvas>
      
      {/* F1/F2 Scale (axis labels) */}
      <div className="absolute top-full left-0 w-full flex justify-between text-xs font-mono pt-1">
        <span>F1 (High)</span>
        <span>F1 (Low)</span>
      </div>
      <div className="absolute top-0 right-full h-full flex flex-col justify-between text-xs font-mono text-right pr-1" style={{ width: "60px" }}>
        <span>F2 (High)</span>
        <span>F2 (Low)</span>
      </div>
    </div>
  );
}
