import { useState, useEffect } from "react";
import PermissionRequest from "./PermissionRequest";
import Spectrogram from "./Spectrogram";
import FormantTracker from "./FormantTracker";
import { Slider } from "@/components/ui/slider";
import { InfoIcon } from "lucide-react";

type AnalysisState = {
  isRecording: boolean;
  audioContext: AudioContext | null;
  analyser: AnalyserNode | null;
  mediaStream: MediaStream | null;
  source: MediaStreamAudioSourceNode | null;
  formants: {
    f1: number;
    f2: number;
    vowel: string;
  };
  config: {
    gain: number;
    brightness: number;
    timeRange: number;
    fftSize: number;
    minFrequency: number;
    maxFrequency: number;
  };
};

const initialState: AnalysisState = {
  isRecording: false,
  audioContext: null,
  analyser: null,
  mediaStream: null,
  source: null,
  formants: {
    f1: 0,
    f2: 0,
    vowel: "--",
  },
  config: {
    gain: 100,
    brightness: 100,
    timeRange: 7,
    fftSize: 2048,
    minFrequency: 0,
    maxFrequency: 5000,
  },
};

export default function VoiceAnalyzer() {
  const [micPermissionGranted, setMicPermissionGranted] = useState(false);
  const [analysisState, setAnalysisState] = useState<AnalysisState>(initialState);
  const [currentTime, setCurrentTime] = useState<string>("00:00.000");

  // Request microphone access
  const requestMicrophoneAccess = async () => {
    try {
      // Create audio context
      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      
      // Request microphone permission
      const mediaStream = await navigator.mediaDevices.getUserMedia({ audio: true, video: false });
      
      // Set up audio processing nodes
      const source = audioContext.createMediaStreamSource(mediaStream);
      const analyser = audioContext.createAnalyser();
      analyser.fftSize = analysisState.config.fftSize;
      analyser.smoothingTimeConstant = 0.5;
      source.connect(analyser);
      
      setAnalysisState(prev => ({
        ...prev,
        isRecording: true,
        audioContext,
        mediaStream,
        source,
        analyser
      }));
      setTimeout(updateConfig, 100); 
      setMicPermissionGranted(true);
      
    } catch (error) {
      console.error("Error accessing microphone:", error);
    }
  };

  const updateFormants = (f1: number, f2: number, vowel: string) => {
    setAnalysisState(prev => ({
      ...prev,
      formants: {
        f1,
        f2,
        vowel
      }
    }));
  };

  // Update configuration values
  const updateConfig = (key: keyof AnalysisState['config'], value: number) => {
    setAnalysisState(prev => ({
      ...prev,
      config: {
        ...prev.config,
        [key]: value
      }
    }));
  };

  // Update current time
  useEffect(() => {
    if (analysisState.isRecording) {
      const intervalId = setInterval(() => {
        const now = new Date();
        setCurrentTime(now.toLocaleTimeString([], { hour12: false }));
      }, 1000);
      
      return () => clearInterval(intervalId);
    }
  }, [analysisState.isRecording]);

  // Clean up audio on unmount
  useEffect(() => {
    return () => {
      if (analysisState.mediaStream) {
        analysisState.mediaStream.getTracks().forEach(track => track.stop());
      }
      if (analysisState.audioContext) {
        analysisState.audioContext.close();
      }
    };
  }, []);

  return (
    <div>
      {!micPermissionGranted ? (
        <PermissionRequest onRequestPermission={requestMicrophoneAccess} />
      ) : (
        <div>
          {/* Visualizations Container */}
          <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
            {/* Spectrogram Section */}
            <div className="lg:col-span-3 bg-white rounded-lg shadow-md p-4">
              <div className="flex justify-between items-center mb-3">
                <h2 className="text-lg font-semibold">Spectrogram with Formants</h2>
                <div className="text-xs font-mono text-muted-foreground">{currentTime}</div>
              </div>
              
              <Spectrogram 
                analyser={analysisState.analyser}
                config={analysisState.config}
                onFormantDetected={updateFormants}
              />
              
              {/* Spectrogram Controls */}
              <div className="mt-4 flex flex-wrap gap-4">
                <div className="w-full max-w-xs">
                  <label htmlFor="gainSlider" className="block text-xs text-muted-foreground mb-1">Input Gain</label>
                  <Slider 
                    id="gainSlider"
                    min={0}
                    max={200}
                    step={1}
                    value={[analysisState.config.gain]}
                    onValueChange={(value) => updateConfig('gain', value[0])}
                  />
                </div>
                <div className="w-full max-w-xs">
                  <label htmlFor="brightnessSlider" className="block text-xs text-muted-foreground mb-1">Brightness</label>
                  <Slider 
                    id="brightnessSlider"
                    min={0}
                    max={200}
                    step={1}
                    value={[analysisState.config.brightness]}
                    onValueChange={(value) => updateConfig('brightness', value[0])}
                  />
                </div>
                <div className="w-full max-w-xs">
                  <label htmlFor="timeRangeSlider" className="block text-xs text-muted-foreground mb-1">Time Display (sec)</label>
                  <Slider 
                    id="timeRangeSlider"
                    min={5}
                    max={10}
                    step={1}
                    value={[analysisState.config.timeRange]}
                    onValueChange={(value) => updateConfig('timeRange', value[0])}
                  />
                  <span className="text-xs font-mono">{analysisState.config.timeRange} seconds</span>
                </div>
              </div>
            </div>
            
            {/* Vowel Chart Section */}
            <div className="lg:col-span-1 bg-white rounded-lg shadow-md p-4">
              <h2 className="text-lg font-semibold mb-3">Formant Tracker</h2>
              
              <FormantTracker 
                f1={analysisState.formants.f1} 
                f2={analysisState.formants.f2} 
              />
              
              {/* Current Formant Values */}
              <div className="mt-4 p-3 bg-muted rounded-md">
                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div className="font-semibold">F1:</div>
                  <div className="font-mono text-right">
                    {analysisState.formants.f1 > 0 ? `${Math.round(analysisState.formants.f1)} Hz` : '--'}
                  </div>
                  <div className="font-semibold">F2:</div>
                  <div className="font-mono text-right">
                    {analysisState.formants.f2 > 0 ? `${Math.round(analysisState.formants.f2)} Hz` : '--'}
                  </div>
                  <div className="font-semibold">Detected:</div>
                  <div className="font-mono text-right font-bold text-secondary">
                    {analysisState.formants.vowel}
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Information Panel */}
          <div className="mt-6 bg-white rounded-lg shadow-md p-4">
            <div className="flex items-center mb-2">
              <InfoIcon className="text-primary mr-2 h-5 w-5" />
              <h3 className="font-semibold">About this Analyzer</h3>
            </div>
            <p className="text-sm text-foreground mb-2">
              This tool analyzes your voice in real-time, extracting the first two formants (F1 and F2) from your speech.
              Formants are concentrations of acoustic energy around particular frequencies, and they're particularly important in distinguishing vowel sounds.
            </p>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-4">
              <div>
                <h4 className="text-sm font-semibold mb-1">Spectrogram</h4>
                <p className="text-xs text-muted-foreground">
                  The spectrogram shows a time-frequency representation of your voice.
                  Brighter areas indicate stronger frequency components. F1 and F2 formants are highlighted with colored lines.
                </p>
              </div>
              <div>
                <h4 className="text-sm font-semibold mb-1">Vowel Chart</h4>
                <p className="text-xs text-muted-foreground">
                  The F1/F2 chart maps your current formant values onto standard vowel positions.
                  Different vowels occupy distinct regions in this acoustic space.
                </p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
