import { useRef, useEffect, useState } from "react";
import { detectFormants } from "@/lib/formantDetection";

interface SpectrogramProps {
  analyser: AnalyserNode | null;
  config: {
    gain: number;
    brightness: number;
    timeRange: number;
    fftSize: number;
    minFrequency: number;
    maxFrequency: number;
  };
  onFormantDetected: (f1: number, f2: number, vowel: string) => void;
}

interface SpectrogramDataPoint {
  frequencyData: Uint8Array;
  formants: {
    f1: number;
    f2: number;
  };
  brightness: number;
}

export default function Spectrogram({ analyser, config, onFormantDetected }: SpectrogramProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const requestRef = useRef<number>();
  const spectrogramBufferRef = useRef<SpectrogramDataPoint[]>([]);
  const [canvasSize, setCanvasSize] = useState({ width: 0, height: 0 });

  // Vowel reference data
  const vowelData = [
    { vowel: 'i', f1: 280, f2: 2250, label: 'ee' },
    { vowel: '', f1: 400, f2: 2000, label: 'i' },
    { vowel: 'e', f1: 400, f2: 1900, label: 'ay' },
    { vowel: '', f1: 550, f2: 1800, label: 'eh' },
    { vowel: '', f1: 700, f2: 1700, label: 'a' },
    { vowel: '', f1: 700, f2: 1100, label: 'ah' },
    { vowel: '', f1: 600, f2: 900, label: 'aw' },
    { vowel: 'o', f1: 450, f2: 900, label: 'oh' },
    { vowel: '', f1: 380, f2: 950, label: 'u' },
    { vowel: 'u', f1: 310, f2: 870, label: 'oo' },
    { vowel: '', f1: 600, f2: 1200, label: 'uh' },
    { vowel: '', f1: 500, f2: 1400, label: 'er' }
  ];

  // Find closest vowel to given formant values
  function findClosestVowel(f1: number, f2: number) {
    if (!f1 || !f2) return null;
    
    let closestVowel = null;
    let minDistance = Infinity;
    
    vowelData.forEach(vowel => {
      // Calculate Euclidean distance in F1/F2 space
      const distance = Math.sqrt(
        Math.pow((f1 - vowel.f1) / 300, 2) + 
        Math.pow((f2 - vowel.f2) / 700, 2)
      );
      
      if (distance < minDistance) {
        minDistance = distance;
        closestVowel = vowel;
      }
    });
    
    // Only return if the distance is reasonably close
    return minDistance < 1.2 ? closestVowel : null;
  }

  // Color mapping function
  function getColor(value: number, brightness: number) {
    value = Math.min(255, value * brightness / 100);
    if (value < 40) return `rgb(0, 0, ${value * 2})`;
    if (value < 128) return `rgb(0, ${value}, ${255 - value})`;
    if (value < 192) return `rgb(${value - 128}, ${value}, 0)`;
    return `rgb(${value}, ${255 - (value/2)}, 0)`;
  }

  // Initialize canvas size
  useEffect(() => {
    const updateSize = () => {
      if (canvasRef.current) {
        const dpr = window.devicePixelRatio || 1;
        const canvas = canvasRef.current;
        const container = canvas.parentElement;
        
        if (container) {
          const width = container.clientWidth;
          const height = container.clientHeight;
          
          // Set canvas size accounting for device pixel ratio
          canvas.width = width * dpr;
          canvas.height = height * dpr;
          
          // Scale context to match CSS size
          const ctx = canvas.getContext('2d');
          if (ctx) {
            ctx.scale(dpr, dpr);
          }
          
          setCanvasSize({ width, height });
        }
      }
    };
   // Run initial size update after a slight delay to ensure DOM is ready
  updateSize();

  // Set up ResizeObserver for dynamic resizing
  const resizeObserver = new ResizeObserver(updateSize);
  if (canvasRef.current?.parentElement) {
    resizeObserver.observe(canvasRef.current.parentElement);
  }

  // Cleanup
  return () => {
    resizeObserver.disconnect();
  };
 
  }, []);

  // Draw spectrogram
  const drawSpectrogram = () => {
    if (!canvasRef.current || canvasSize.width === 0 || canvasSize.height === 0) return;
    
    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;
    
    // Clear canvas
    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, canvasSize.width, canvasSize.height);
    
    const buffer = spectrogramBufferRef.current;
    if (buffer.length === 0) return;
    
    const columnWidth = canvasSize.width / buffer.length;
    
    // Draw each column of the spectrogram
    buffer.forEach((column, columnIndex) => {
      const x = columnIndex * columnWidth;
      
      // Draw frequency data
      if (column.frequencyData) {
        for (let i = 0; i < column.frequencyData.length; i++) {
          // Map frequency bin to y-coordinate (inverted, as higher freqs should be at the top)
          const normalizedBin = i / column.frequencyData.length;
          const frequencyRatio = config.minFrequency + normalizedBin * (config.maxFrequency - config.minFrequency);
          const y = canvasSize.height - (frequencyRatio / config.maxFrequency * canvasSize.height);
          
          // Draw pixel
          const value = column.frequencyData[i];
          if (value > 0) {
            ctx.fillStyle = getColor(value, column.brightness);
            ctx.fillRect(x, y, columnWidth + 1, 1);
          }
        }
      }
      
      // Draw formants as colored lines
      if (column.formants.f1 > 0) {
        const y1 = canvasSize.height - (column.formants.f1 / config.maxFrequency * canvasSize.height);
        ctx.strokeStyle = '#FF4081'; // Pink for F1
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.moveTo(x, y1);
        ctx.lineTo(x + columnWidth, y1);
        ctx.stroke();
      }
      
      if (column.formants.f2 > 0) {
        const y2 = canvasSize.height - (column.formants.f2 / config.maxFrequency * canvasSize.height);
        ctx.strokeStyle = '#2196F3'; // Blue for F2
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.moveTo(x, y2);
        ctx.lineTo(x + columnWidth, y2);
        ctx.stroke();
      }
    });
  };

  // Animation loop for audio analysis
  const analyzeAudio = () => {
    if (!analyser) {
      requestRef.current = requestAnimationFrame(analyzeAudio);
      return;
    }
    
    // Get frequency data
    const bufferLength = analyser.frequencyBinCount;
    const dataArray = new Uint8Array(bufferLength);
    analyser.getByteFrequencyData(dataArray);
    
    // Process data with gain applied
    const processedData = new Uint8Array(dataArray.length);
    for (let i = 0; i < dataArray.length; i++) {
      processedData[i] = Math.min(255, dataArray[i] * config.gain / 100);
    }
    
    // Detect formants
    const formants = detectFormants(processedData, analyser.context.sampleRate, analyser.frequencyBinCount);
    
    // Add to buffer
    spectrogramBufferRef.current.push({
      frequencyData: processedData,
      formants,
      brightness: config.brightness
    });
    
    // Max buffer based on time range (assuming ~60fps)
    const maxBufferLength = Math.ceil(config.timeRange * 60);
    
    // Remove oldest data if buffer exceeds max length
    if (spectrogramBufferRef.current.length > maxBufferLength) {
      spectrogramBufferRef.current.shift();
    }
    
    // Draw updated spectrogram
    drawSpectrogram();
    
    // Find closest vowel
    if (formants.f1 > 0 && formants.f2 > 0) {
      const closestVowel = findClosestVowel(formants.f1, formants.f2);
      
      // Update parent component with formant data
      onFormantDetected(
        formants.f1, 
        formants.f2, 
        closestVowel ? closestVowel.label : '--'
      );
    }
    
    requestRef.current = requestAnimationFrame(analyzeAudio);
  };

  // Start/stop animation
  useEffect(() => {
    requestRef.current = requestAnimationFrame(analyzeAudio);
    return () => {
      if (requestRef.current) {
        cancelAnimationFrame(requestRef.current);
      }
    };
  }, [analyser, config]);

  // Reset buffer when time range changes
  useEffect(() => {
    spectrogramBufferRef.current = [];
  }, [config.timeRange]);

  return (
    <div className="relative w-full bg-black rounded-md" style={{ height: "280px" }}>
      <canvas ref={canvasRef} className="absolute top-0 left-0 w-full h-full"></canvas>
      {/* Frequency Scale (Y-axis) */}
      <div className="absolute top-0 right-full pr-2 h-full flex flex-col justify-between text-xs font-mono text-right" style={{ width: "50px" }}>
        <span>5 kHz</span>
        <span>4 kHz</span>
        <span>3 kHz</span>
        <span>2 kHz</span>
        <span>1 kHz</span>
        <span>0 Hz</span>
      </div>
    </div>
  );
}
