/**
 * Find peaks (local maxima) in frequency data
 */
export function findPeaks(frequencies: { frequency: number, magnitude: number }[]): { frequency: number, magnitude: number }[] {
  const peaks: { frequency: number, magnitude: number }[] = [];
  const threshold = 5; // Minimum magnitude to consider
  
  for (let i = 2; i < frequencies.length - 2; i++) {
    const curr = frequencies[i];
    const prev1 = frequencies[i - 1];
    const prev2 = frequencies[i - 2];
    const next1 = frequencies[i + 1];
    const next2 = frequencies[i + 2];
    
    // Check if current point is a local maximum
    if (curr.magnitude > threshold &&
        curr.magnitude > prev1.magnitude && curr.magnitude > prev2.magnitude &&
        curr.magnitude > next1.magnitude && curr.magnitude > next2.magnitude) {
      peaks.push(curr);
    }
  }
  
  return peaks;
}

/**
 * Detect formants from frequency data
 */
export function detectFormants(
  frequencyData: Uint8Array, 
  sampleRate: number,
  frequencyBinCount: number
): { f1: number, f2: number } {
  // Convert frequency data to array of frequencies with their magnitudes
  const frequencies: { frequency: number, magnitude: number }[] = [];
  const binSize = sampleRate / (frequencyBinCount * 2);
  
  for (let i = 0; i < frequencyData.length; i++) {
    const frequency = i * binSize;
    const magnitude = frequencyData[i];
    
    // Only consider frequencies within vocal range (80Hz to 5000Hz)
    if (frequency >= 80 && frequency <= 5000) {
      frequencies.push({ frequency, magnitude });
    }
  }
  
  // Find local maxima in the spectrum (potential formants)
  const peaks = findPeaks(frequencies);
  
  // Typical formant ranges
  const f1Range = { min: 200, max: 900 };
  const f2Range = { min: 800, max: 2500 };
  
  // Find strongest peak in F1 range
  let f1 = 0;
  let f1Magnitude = 0;
  peaks.forEach(peak => {
    if (peak.frequency >= f1Range.min && peak.frequency <= f1Range.max && peak.magnitude > f1Magnitude) {
      f1 = peak.frequency;
      f1Magnitude = peak.magnitude;
    }
  });
  
  // Find strongest peak in F2 range that's higher than F1
  let f2 = 0;
  let f2Magnitude = 0;
  peaks.forEach(peak => {
    if (peak.frequency >= f2Range.min && peak.frequency <= f2Range.max && 
        peak.frequency > f1 + 200 && peak.magnitude > f2Magnitude) {
      f2 = peak.frequency;
      f2Magnitude = peak.magnitude;
    }
  });
  
  return { f1, f2 };
}
